import React, { Component } from 'react';
import './style.css';

class Demo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: null
    };
  }

  vote(answer) {
    this.setState({
      state: {
        selected: answer
      }
    });
  }

  render() {
    return (
      <div className="view-container">
        <div className="view">
          <div className="vote">
            <div className="category">Pertanian</div>
            <div className="question">Export kelapa sawit harus dikurangi</div>
            <div className="answer-options">
              <button
                className="option {this.isSelected('agree')} "
                onClick={() => this.vote('agree')}
              >
                Setuju
              </button>
              <button
                className="option {this.isSelected('disagree')} "
                onClick={() => this.vote('disagree')}
              >
                Tidak setuju
              </button>
              <button
                className="option {this.isSelected('abstain')} "
                onClick={() => this.vote('abstain')}
              >
                Netral
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Demo;
