import React, { Component } from 'react';
import Parallax from 'react-rellax';

import '../../../../../fonts/bangers.css';
import styles from './styles.module.css';
import imageTalkBubble from './images/talk-bubbla.png';

export class Bubble extends Component {
  constructor(props) {
    super(props);

    this.style = {
      width: props.data.size.width,
      height: props.data.size.height,
      transform: 'scale(' + props.data.scale + ')'
    };

    if (props.data.flip) {
      this.imageStyle = {
        transform: 'scaleX(-1)'
      };
    }
  }

  componentDidMount() {
    const self = this;
    this.timer = setTimeout(() => {
      self.props.data.removeFunction(self.props.data.id);
    }, self.props.data.lifeSpanInMs);
  }

  render() {
    return (
      <Parallax speed={this.props.scrollSpeed}>
        <div className={['', styles.bubble].join(' ')} style={this.style}>
          <div className={styles.text}>{this.props.data.text}</div>
          <img
            className={styles.talk_bubble}
            src={imageTalkBubble}
            alt="talk-bubble"
            style={this.imageStyle}
          />
        </div>
      </Parallax>
    );
  }
}
