import React from 'react';
import Demo from './components/demo';
import { PopupCanvas } from './components/popup-canvas';
import styles from './styles.module.css';

class WelcomeSection extends React.Component {
  domContainer = React.createRef();
  textContainer = React.createRef();

  constructor(props) {
    super(props);
    this.state = {
      containerRendered: false,
      containerSize: {
        width: null,
        height: null
      }
    };
  }

  async componentDidMount() {
    this.setState({
      containerRendered: true
    });
  }

  renderPopupCanvas() {}

  render() {
    return (
      <div className={styles.outerContainer}>
        <div className={styles.container} ref={this.domContainer}>
          {this.state.containerRendered && (
            <PopupCanvas
              parentContainer={this.domContainer.current}
              textContainer={this.textContainer.current}
            />
          )}
          <div className={['wrapper', styles.content].join(' ')}>
            <div>
              <Demo />
            </div>
            <div className={styles.text}>
              <span ref={this.textContainer}>
                Terima kasih sama sama apakah maka dari itu makanya begitu
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default WelcomeSection;
