import React, { Component } from 'react';
import Parallax from 'react-rellax';

import styles from './styles.module.css';
import imageDate17April from './images/date-17april.png';

class SubscribeSection extends Component {
  render() {
    const onScroll = coordinate => {
      console.log('xy', coordinate);
    };
    return (
      <div className={styles.outerContainer}>
        <div className={['wrapper', styles.wrapper].join(' ')}>
          <div className={styles.content}>
            <div className={styles.title}>
              <h1 className={styles.h1}>Save the date</h1>
            </div>
            <div className={styles.text}>
              <p>
                Pemilihan Umum Presiden dan Legislatif akan diselenggarakan
                serentak untuk pertama kali nya pada tanggal 17 April 2019,
                jangan sampai kamu ketinggalan ikut berpartisipasi di momen
                bersejarah ini.
              </p>
              <p>
                Jangan lupa cek dulu visi misi calon legislatif yang akan kamu
                pilih sebelum mencoblos. Jangan lagi kita memilih representatif
                yang tak memperjuangkan hak dan suara kamu.
              </p>
            </div>
          </div>
          <Parallax speed={4} onMove={onScroll}>
            <img
              className={styles.date17april}
              src={imageDate17April}
              alt="pemilu-17-april"
            />
          </Parallax>
        </div>
      </div>
    );
  }
}

export default SubscribeSection;
