import React, { Component } from 'react';

import styles from './styles.module.css';

class SubscribeSection extends Component {
  render() {
    return (
      <div className={styles.outerContainer}>
        <div className={['wrapper', styles.wrapper].join(' ')}>
          <div className={styles.content}>
            <div className={styles.title}>
              <h1 className={styles.h1}>Kabarin dong</h1>
            </div>
            <div className={styles.text}>
              <p>
                Buat kalian yang masih belum tau apasih visi misi calon-calon
                legislatif Pemilu 2019 nanti, cek aja langsung di SuaraKami.
              </p>
              <p>
                Buat kalian yang masih belum tau apasih visi misi calon-calon
                legislatif Pemilu 2019 nanti, cek aja langsung di SuaraKami.
                Menjelang Pemilu 2019 nanti, aplikasi SuaraKami akan dapat
                diakses untuk semua masyarakat Indonesia di penjuru dunia.
                Tunggu aja tanggal mainnya!
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SubscribeSection;
