import React from 'react';
import _ from 'underscore';
import { Bubble } from './bubble';
import { getRandomInt } from '../../../../utils/getRandomInt';
import { Transition, animated } from 'react-spring';
import styles from './styles.module.css';

export class PopupCanvas extends React.Component {
  textCounter = 0;
  texts = [
    'Gimana sih. Ah,',
    'Mana tuh',
    'Sungainya penuh sampah',
    'Korupsi terus'
  ];
  textBubbleSize = {
    width: 190,
    height: 120
  };

  constructor(props) {
    super(props);

    if (props.parentContainer == null) {
      throw new Error('Missing parent container element');
    }

    this.state = {
      popupBubbles: [],
      canvasDimension: {
        width: props.parentContainer.offsetWidth,
        height: props.parentContainer.offsetHeight
      }
    };

    this.spawnTextBubbles = this.spawnTextBubbles.bind(this);
    this.addNewBubble = this.addNewBubble.bind(this);
    this.removeBubble = this.removeBubble.bind(this);
  }

  componentDidMount() {
    const bubbleWidth = 190;
    const bubbleHeight = 120;
    const canvasWidthMinusBubbleWidth =
      this.state.canvasDimension.width - bubbleWidth;
    const canvasHeightMinusBubbleHeight =
      this.state.canvasDimension.height - bubbleHeight;

    const bubble = {
      id: _.uniqueId(),
      text: this.texts[0],
      size: {
        width: 190,
        height: getRandomInt(100, 120)
      },
      coordinate: this.findRandomLocationOutsideTextContainer(
        canvasWidthMinusBubbleWidth,
        canvasHeightMinusBubbleHeight
      ),
      removeFunction: this.removeBubble,
      lifeSpanInMs: 1000
    };
    this.addNewBubble(bubble);

    // Generate bubble
    this.spawnTextBubbles();
  }

  componentWillUnmount() {}

  spawnTextBubbles() {
    const spawnInterval = 5000;
    const bubbleSpawnCount = getRandomInt(2, 5);
    const canvasWidthMinusBubbleWidth =
      this.state.canvasDimension.width - this.textBubbleSize.width;
    const canvasHeightMinusBubbleHeight =
      this.state.canvasDimension.height - this.textBubbleSize.height;

    for (let index = 0; index < bubbleSpawnCount; index++) {
      const bubble = {
        id: _.uniqueId(),
        text: this.texts[this.textCounter],
        scale: getRandomInt(6, 10) * 0.1,
        size: {
          width: getRandomInt(150, 180),
          height: getRandomInt(100, 120)
        },
        coordinate: this.findRandomLocationOutsideTextContainer(
          canvasWidthMinusBubbleWidth,
          canvasHeightMinusBubbleHeight
        ),
        flip: getRandomInt(0, 1) === 1,
        lifeSpanInMs: getRandomInt(4, 6) * 1000,
        scrollSpeed: getRandomInt(-6, 6),
        removeFunction: this.removeBubble
      };

      this.textCounter =
        this.textCounter < this.texts.length - 1 ? this.textCounter + 1 : 0;

      setTimeout(() => this.addNewBubble(bubble), getRandomInt(500, 2000));
    }

    setTimeout(this.spawnTextBubbles, spawnInterval);
  }

  addNewBubble(bubbleData) {
    this.setState(state => ({
      popupBubbles: [...state.popupBubbles, bubbleData]
    }));
  }

  removeBubble(id) {
    this.setState(state => ({
      popupBubbles: state.popupBubbles.filter(el => el.id !== id)
    }));
  }

  findRandomLocationOutsideTextContainer(
    canvasWidthMinusBubbleWidth,
    canvasHeightMinusBubbleHeight
  ) {
    const textContainerLoc = this.props.textContainer.getBoundingClientRect();
    const textComponentYOffset = 120;
    const textComponentYPadding = 40;

    const textComponentYBoundaries = {
      start:
        textContainerLoc.top - textComponentYOffset - textComponentYPadding,
      end:
        textContainerLoc.top -
        textComponentYOffset -
        textComponentYPadding * 2 +
        textContainerLoc.height
    };

    let coordinates = null;
    do {
      coordinates = {
        x: getRandomInt(0, canvasWidthMinusBubbleWidth),
        y: getRandomInt(0, canvasHeightMinusBubbleHeight)
      };
      console.log(
        'Found coord :',
        canvasHeightMinusBubbleHeight,
        coordinates,
        textComponentYBoundaries
      );
    } while (
      coordinates.y + this.textBubbleSize.height >
        textComponentYBoundaries.start &&
      coordinates.y < textComponentYBoundaries.end
    );

    return coordinates;
  }

  renderBubbles() {
    return bubbleData => styles => {
      const bubbleLocation = {
        position: 'absolute',
        left: bubbleData.coordinate.x,
        top: bubbleData.coordinate.y
      };

      return (
        <animated.div style={bubbleLocation}>
          <div style={styles}>
            <Bubble key={bubbleData.id} data={bubbleData} />
          </div>
        </animated.div>
      );
    };
  }

  render() {
    const animation = {
      from: { opacity: 1, transform: 'scale(0.0)' },
      enter: [{ opacity: 1, transform: 'scale(1)' }],
      leave: { opacity: 0 }
    };

    return (
      <div
        className={styles.container}
        style={{ ...this.state.canvasDimension }}
      >
        <Transition
          items={this.state.popupBubbles}
          keys={el => el.id}
          from={animation.from}
          enter={animation.enter}
          leave={animation.leave}
          config={{ tension: 200, friction: 12, precision: 0.01 }}
        >
          {this.renderBubbles()}
        </Transition>
      </div>
    );
  }
}
