import React, { Component } from 'react';
import './style.css';

class MenuBar extends Component {
  menus = [
    {
      name: 'Kenapa',
      link: ''
    },
    {
      name: 'FAQ',
      link: ''
    },
    {
      name: 'Subscribe',
      link: ''
    }
  ];

  renderMenu() {
    return this.menus.map((menu, index) => (
      <div className="link" key={index}>
        {menu.name}
      </div>
    ));
  }

  render() {
    return (
      <div className="menu">
        <div className="logo">
          <a href="/" title="SuaraKami">
            SuaraKami
          </a>
        </div>
        <div className="links">{this.renderMenu()}</div>
      </div>
    );
  }
}

export default MenuBar;
